@import "../../../assets/styles/mixin.scss";
@import "../../../assets/styles/variable.scss";

.com-ourgame {
  padding-bottom: 1rem;
  font-family: "Montserrat", sans-serif;
  margin-top: 2rem;
  .title {
    font-size: 2rem;
    font-weight: 800;
    text-transform: uppercase;
    margin: 0 $pdMobile;
    margin-bottom: 1rem;

    @include textGlitch("titleOurgame", 17, black, rgba(0, 0, 0, 0.001), rgba(255, 0, 0, 0.3), rgba(0, 0, 255, 0.3), 400, 100);
  }
  .content {
    font-size: 1rem;
    font-style: italic;
    color: #5e5e5e;
    font-weight: 500;
    padding: 0 $pdMobile;
  }
  .block-item {
    .item {
      padding: 0 $pdMobile;
      img {
        border-radius: 30px;
        width: 100%;
        max-width: 800px;
        height: 200px;
        object-fit: cover;
        object-position: center;
        margin: 1rem 0;
        @include mobilePlus() {
          height: 350px;
          margin: 1rem auto;
        }
      }
      figcaption {
        font-family: "Montserrat", sans-serif;
        font-size: 1.5rem;
        font-weight: 800;
        text-align: center;
      }
    }
  }
  .slick-list {
    margin: 1rem 0;
  }
  .slick-dots {
    bottom: -50px;
  }
  .btn-ourgame {
    @include btn();
    margin: 2rem $pdMobile;
    margin-top: 5rem;
    @include mobilePlus() {
      max-width: 800px;
      margin: 5rem auto;
      margin-bottom: 2rem;
    }
  }
}

@include desktop {
  .com-ourgame {
    padding: 0 $pdDesk;
    padding-bottom: 5rem;
    .title {
      font-size: 3rem;
      margin-left: 0;
      @include textGlitch("titleOurgame", 17, black, rgba(0, 0, 0, 0.001), rgba(255, 0, 0, 0.3), rgba(0, 0, 255, 0.3), 800, 100);
      @include desktop2k {
        font-size: 5rem;
      }
    }
    .content {
      font-size: 1rem;
      padding: 0;
    }
    .block-item {
      .item {
        // cursor: pointer;
        padding: unset;
        img {
          width: 100%;
          height: 400px;
          object-fit: cover;
          transition: 0.4s all ease-in-out !important;
          @include desktop2k() {
            height: 900px;
            max-width: unset;
          }
        }

        figcaption {
          font-size: 2rem;
          text-align: left;
          padding-left: 0;
          transition: 0.4s all ease-in-out !important;
          @include desktop2k {
            font-size: 3rem;
          }
        }
        &.left {
          img {
            object-position: left;
          }
        }
        &:hover {
          img {
            box-shadow: 0 20px 20px rgba(0, 0, 0, 0.07);
            transform: translateY(-10px);
          }
          figcaption {
            color: $hong-color;
          }
        }
      }
    }
    .slick-slider {
      .slick-track {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 3rem;
        max-width: 80vw;
        min-width: 1000px;
        &::before {
          display: unset;
          content: unset;
        }
        .slick-slide {
          width: unset !important;
        }
      }
    }

    .slick-list {
      margin: 1rem 0;
    }
    .slick-dots {
      bottom: -50px;
    }
    .btn-ourgame {
      max-width: 400px;
      margin: auto;
      @include btnDesk();
      @include btn-effect($hong-color);
    }
  }
}
