@mixin mobile {
  @media (min-width: 300px) {
    @content;
  }
}
@mixin mobilePlus {
  @media (min-width: 540px) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin ipadpro {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: 1280px) {
    @content;
  }
}
@mixin desktop2k {
  @media (min-width: 2500px) {
    @content;
  }
}
@mixin desktop4k {
  @media (min-width: 3500px) {
    @content;
  }
}
@mixin textGlitch($name, $intensity, $textColor, $background, $highlightColor1, $highlightColor2, $width, $height) {
}

@mixin imgGlitch($name, $intensity, $width, $height, $top, $left) {
  $steps: $intensity;

  // Ensure the @keyframes are generated at the root level
  @at-root {
    // We need two different ones
    @for $i from 1 through 2 {
      @keyframes #{$name}-anim-#{$i} {
        @for $i from 0 through $steps {
          #{percentage($i*(1/$steps))} {
            clip: rect(random($height) + px, $width + px, random($height) + px, 0);
          }
        }
      }
    }
  }

  > img {
    position: absolute;
    top: $top + px;
    left: $left + px;
  }
  > img:nth-child(2),
  > img:nth-child(3) {
    clip: rect(0, 0, 0, 0);
  }
  > img:nth-child(2) {
    left: ($left + 2) + px;
    animation: #{$name}-anim-1 2s infinite linear alternate-reverse;
  }
  > img:nth-child(3) {
    left: ($left - 2) + px;
    animation: #{$name}-anim-2 3s infinite linear alternate-reverse;
  }
}

@mixin svgGlitch($name, $intensity, $fillColor, $background, $fillHighlight1, $fillHighlight2, $width, $height, $top, $left) {
  $steps: $intensity;

  @at-root {
    // We need two different ones
    @for $i from 1 through 2 {
      @keyframes #{$name}-anim-#{$i} {
        @for $i from 0 through $steps {
          #{percentage($i*(1/$steps))} {
            clip: rect((random($height)-20) + px, $width + px, (random($height) + 20) + px, 0);
          }
        }
      }
    }
  }
}

@mixin btn-effect($color) {
  $color-light: lighten($color, 10%);
  $color-lightest: lighten($color, 25%);
  transition: 0.4s all ease-in-out;
  &:hover {
    box-shadow: 5px 5px 0px 0px $color-light, 10px 10px 0px 0px $color-lightest;
    transform: translateY(-10px) translateX(-10px);
  }
}

@mixin btn() {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: 600;
  color: white;
  background-color: $hong-color;
  text-transform: uppercase;
  border-radius: 33px;
  padding: 1rem 1.25rem;
  user-select: none;
  cursor: pointer;
}

@mixin btnDesk() {
  max-width: 400px;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  color: white;
  background-color: $hong-color;
  border-radius: 33px;
  padding: 1.25rem 2.5rem;
  user-select: none;
  cursor: pointer;
  @include desktop2k() {
    font-size: 1.5rem;
    max-width: 800px;
    padding: 2rem 4rem;
    top: unset;
    bottom: 20vh;
  }
  @include desktop4k() {
    bottom: 10vh;
  }
}

@mixin title() {
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  text-transform: uppercase;
  user-select: none;
}
